<script>

    import cTop from './comps/top.vue';
    import cMessage from './comps/message_2.vue';
    import Cbusiness from './comps/business_handling_3.vue';
    import Cguidance from './comps/guidance_4.vue';
    import Cservice from './comps/service-seeking_5.vue';
    import Cnews from './comps/new_6.vue';
    import Cservicebox from './comps/service-seeking_7.vue';
    import Cnav from '@/components/nav.vue';
  

    export default {
        components: {
            cTop,
            cMessage,
            Cbusiness,
            Cguidance,
            Cservice,
            Cnews,
            Cservicebox,
            Cnav
        },
        data() {
            return {
                
            };
        },
        created() {
            
        },
        mounted(){
           
        },
        methods: {
            loadMore() {
               
            },
            send(){

                this.$http
                    .post('/hackathon/infer', {
                        id:111,
                        is_default: 111,
                        tg_default: 1
                    })
                    .then(err=>{
                          console.log(err);
                    });
            },
            getArticleList() {
                let url = '/youyi/common/getArticle';
                let params = this.search;
                this.$http({
                    method: 'get',
                    url,
                    params
                }).then(res => {
                    let list = res.data.articleInfo || [];
                    if (list.length) {
                        list.forEach(item => {
                            this.list.push(item);
                        });
                        this.busy = false;
                        this.search.page++;
                    }
                    else {
                        this.isNoMoreData = true;
                    }
                });
            }
        }
    };
</script>

<style >
